<template>
  <div id="root">
    <div class="title"><span>历史页面</span></div>
    <div id="imgBox">
      <ul class="imgUl">
        <li v-for="(obj, index) in oldData" :key="index">
          <img v-if="obj.layers[obj.layers.length - 1].type === 'image'" :src="obj.layers[obj.layers.length - 1].content"  @click="showOldPage(obj)" style='cursor:pointer'/>
          <div v-else  @click="showOldPage(obj)" style="user-select:none;cursor:pointer">文本封面</div>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
export default {
  name: 'History',
  created () {},
  data () {
    return {
      // 获取保存过的canvasController对象数组
      oldData: this.$store.state.oldData
    }
  },
  props: [],
  methods: {
    showOldPage (obj) {
      if (!this.$bus.canvasShow) {
        this.$Message.info('没有画布呢~')
        return
      }
      this.$store.state.canvasData.fromJSON(obj)
      this.$bus.$emit('historyimgclick', obj)
    }
  }
}
</script>

<style scoped>
#root {
  margin: 2px 0 0 2px;
  border: 1px solid #d6d7de;
  width: 23.86vw;
  height: 85vh;
}
.title {
  width: 18vw;
  height: 4.4vh;
  background-color: #f5c2ca;
  border-radius: 1vh;
  margin: 0 auto;
  margin-top: 3.15vh;
  display: flex;
  justify-content: center;
}
.title > span {
  align-self: center;
}
.description {
  text-align: center;
  font-size: 0.57vw;
  color: #767676;
}

/* 素材样式开始 */
#imgBox {
  width: 23.86vw;
  height: 83vh;
  margin-top: 0.633vh;
  border-top: 1px solid #d6d7de;
  overflow-y: scroll;
}
::-webkit-scrollbar {
  display: none; /* Chrome Safari */
}

.imgUl {
  display: flex;
  flex-wrap: wrap;
  padding: 0;
}
.imgUl > li {
  width: 5.76vw;
  height: 5.76vw;
  list-style: none;
  margin-left: 1.55vw;
  margin-top: 3.16vh;
  position: relative;
}
.imgUl > li > img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

/* 素材样式结束 */
</style>



